function HashTag() {
}

HashTag.prototype = {
    implements : ['ready', 'render', 'decompile'],

    register : function() {
        SYMPHONY.services.make('hashtag-primitive', this, this.implements, true);
    },

    ready : function() {
        this.renderer = SYMPHONY.services.subscribe('message-renderer');
        this.registry = SYMPHONY.services.subscribe('primitive-registry');
        this.decompiler = SYMPHONY.services.subscribe('presentationml-decompiler');
        this.parser = SYMPHONY.services.subscribe('messageml-parser');
        this.registry.registerPrimitive('hashtag', 'hashtag-primitive');
        this.registry.registerPrimitive('cashtag', 'hashtag-primitive');
        this.registry.registerPrimitive('hashTag', 'hashtag-primitive');
        this.registry.registerPrimitive('cashTag', 'hashtag-primitive');
        this.registry.registerPrimitive('hash', 'hashtag-primitive');
        this.registry.registerPrimitive('cash', 'hashtag-primitive');
    },

    render : function(messageId, element, type, data, id, node) {
        var keyword = (data && data[id]) || node['$']['tag'];

        if (!keyword) {
            var html = $('<span>');
            this.parser.parse(messageId, html, node, data);
            keyword = html.text();
        }

        var entity = {
            id: keyword,
            indexStart: 0,
            indexEnd: 0,
            text: keyword,
            type: 'KEYWORD',
        }

        var html = this.renderer.renderEntity('hashtags', entity);
        element.append(html);
    },

    decompile : function(type, node) {
        type = type.toLowerCase();
        var classes = (node['$'] && node['$']['class']) || '';
        var result = '<' + type + ' class="' + classes + '">';
        result += this.decompiler.parse(node);
        result += '</' + type + '>';
        return result;
    }
};

var primitive = new HashTag();
primitive.register();
