var xml2js = require('xml2js');
var parseString = xml2js.parseString;
var Builder = xml2js.Builder;

function MessageMlParser() {
}

MessageMlParser.prototype = {
    implements : ['ready', 'registerPrimitive', 'create', 'parse', 'render', 'renderEntity', 'parseEntity'],

    register : function() {
        SYMPHONY.services.make('messageml-parser', this, this.implements, true);
    },

    ready : function() {
        this.entities = {};
        this.registry = SYMPHONY.services.subscribe('primitive-registry');
    },

    goodNode : function(type) {
        var goodNodes = ['entity', '__text__'];

        if (goodNodes.indexOf(type.toLowerCase()) !== -1) {
            return true;
        }

        return Boolean(this.primitives[type.toLowerCase()]);
    },

    parse : function(messageId, element, node, data, index, length) {
        this.primitives = this.registry.getPrimitives();

        function parseOne(node) {
            if (!node) {
                return;
            }

            var type = node['#name'];

            if (!this.goodNode(type)) {
                return;
            }

            switch(type) {
                case '__text__':
                    element.append($('<span>').text(node._));
                    return;
                default:
                    var id = node['$'] && node['$'].id;
                    var service = this.primitives[type];
                    if (service) {
                        return service.render(messageId, element, type, data, id, node, index, length);
                    } else {
                        console.warn('unknown tag during render', type);
                        return;
                    }
            }
        }

        function parseMany(nodes) {
            _.each(nodes, function(node) {
                parseOne.call(this, node);
            }, this);
        }

        parseMany.call(this, node['$$']);
    },

    renderEngine : function(messageId, template, data, outerNames) {
        var result = $('<span class="' + outerNames[0] + '">');
        var node = this.parseEngine(template, outerNames);

        if (node) {
            this.parse(messageId, result, node, data);
        }

        return result;
    },

    parseEngine : function(template, outerNames)
    {
        var options = {
            charsAsChildren: true,
            includeWhiteChars: true,
            explicitArray: true,
            explicitCharkey: true,
            explicitChildren: true,
            preserveChildrenOrder: true,
        };

        var parsed;

        parseString(template, options, function(err, result) {
            if (err) {
                console.warn('error parsing presentationML', template);
                console.log(err);
            }
            parsed = result;
        });

        if (!parsed) {
            return result;
        }

        for (var idx = 0; idx < outerNames.length; idx++) {
            var name = outerNames[idx];
            if (parsed && parsed[name]) {
                return parsed[name];
            }
        }
        return;
    },

    renderEntity : function(messageId, template, data) {
        return this.renderEngine(messageId, template, data, ['entity', 'messageML']);
    },

    parseEntity : function(template) {
        return this.parseEngine(template, ['entity', 'messageML']);
    },

    render : function(messageId, template, data) {
        return this.renderEngine(messageId, template, data, ['presentationML']);
    }
}

var parser = new MessageMlParser();
parser.register();





