function Entity() {
}

Entity.prototype = {
    implements : ['ready', 'render', 'rerender', 'decompile'],

    register : function() {
        SYMPHONY.services.make('entity-primitive', this, this.implements, true);
    },

    ready : function() {
        this.entityRegistry = SYMPHONY.services.subscribe('entity-registry');
        this.registry = SYMPHONY.services.subscribe('primitive-registry');
        this.renderingRegistry = SYMPHONY.services.subscribe('rendering-registry');
        this.decompiler = SYMPHONY.services.subscribe('presentationml-decompiler');
        this.parser = SYMPHONY.services.subscribe('messageml-parser');
        this.defaultEntity = SYMPHONY.services.subscribe('default-entity');

        this.registry.registerPrimitive('entity', 'entity-primitive');
    },

    rerender : function(wrapper) {
        var element = wrapper.selector;
        var content = '<entity>' + wrapper.content + '</entity>';
        var node = this.parser.parseEntity(content);
        var data = wrapper.data;
        var type = wrapper.type;
        var messageId = wrapper.messageId;

        element.empty();

        this.renderEntity(messageId, element, type, data, node, content);
    },

    renderEntity : function(messageId, element, type, data, node, content) {
        var entityRenderers = this.renderingRegistry.getEntities();

        if (!entityRenderers[type] || !entityRenderers[type].render) {
            console.info('no entity renderer for type ', type);
            this.defaultEntity.render(messageId, element, type, data, node, content);
        } else {
            var service = entityRenderers[type];
            var rendered = service.render(type, data, {id: messageId}, content);
            element.append(rendered);
        }
    },

    render : function(messageId, element, type, data, id, node) {
        var name = (node['$'] && node['$'].name) || '';
        var data = data[name];
        var type = (node['$'] && node['$'].type) || '';
        var content = this.decompiler.parse(node);
        var html = $('<span>');

        if (data) {
            type = data.type || type;
        }

        this.renderEntity(messageId, html, type, data, node, content);
        element.append(html);

        var wrapper = {
            messageType: 'com.symphony.messageml.v2',
            type: type,
            selector : html,
            data : data,
            content: content,
            messageId: messageId
        };

        this.entityRegistry.registerEntity(messageId, type, wrapper);
    },

    decompile : function(type, node) {
        var classes = (node['$'] && node['$']['class']) || '';
        var name = (node['$'] && node['$']['data-entity-id']) || 'default';
        var entityType = (node['$'] && node['$']['type']);
        entityType = (entityType) ? 'type="' + entityType + '"' : '';

        var result = '<' + type + ' class="' + classes + '" ' + entityType + ' name="' + name + '">';
        result += this.decompiler.parse(node);
        result += '</' + type + '>';
        return result;
    }
};

var primitive = new Entity();
primitive.register();
