function PrimitiveRegistry() {
    this.primitives = {};
}

PrimitiveRegistry.prototype = {
    implements : ['ready', 'registerPrimitive', 'getPrimitives', 'classesToPrimitive'],

    register : function() {
        SYMPHONY.services.make('primitive-registry', this, this.implements, true);
    },

    ready : function() {
    },

    registerPrimitive : function(name, serviceName) {
        this.primitives[name.toLowerCase()] = SYMPHONY.services.subscribe(serviceName);
    },

    getPrimitives : function() {
        return this.primitives;
    },

    classesToPrimitive : function(classesStr) {
        var classes = classesStr.toLowerCase().split(' ');
        var types = Object.keys(this.primitives);
        var result = false;

        _.each(classes, function(className) {
            if (types.indexOf(className) !== -1 && !result) {
                result = className;
            }
        }, this);

        return result;
    }
};

var registry = new PrimitiveRegistry();
registry.register();
