var parseString = require('xml2js').parseString;

function FormattedText() {
}

FormattedText.prototype = {
    implements : ['ready', 'render', 'decompile'],

    register : function() {
        SYMPHONY.services.make('formatted-text-primitive', this, this.implements, true);
    },

    ready : function() {
        this.registry = SYMPHONY.services.subscribe('primitive-registry');
        this.decompiler = SYMPHONY.services.subscribe('presentationml-decompiler');
        this.parser = SYMPHONY.services.subscribe('messageml-parser');
        this.registry.registerPrimitive('formatted', 'formatted-text-primitive');
    },

    render : function(messageId, element, type, data, id, node) {
        if (data[id]) {
            var xml = data[id];

            xml = '<formatted>' + xml + '</formatted>';

            var options = {
                charsAsChildren: true,
                explicitArray: true,
                explicitCharkey: true,
                explicitChildren: true,
                preserveChildrenOrder: true,
            };

            var parsed;

            parseString(xml, options, function(err, result) {
                parsed = result;
            });

            var node = parsed && parsed.formatted;

            this.parser.parse(messageId, element, node, data);
        } else {
            var html = '<span>';
            this.parser.parse(messageId, html, node, data);
            element.append(html);
        }
    },

    decompile : function(type, node) {
        var classes = (node['$'] && node['$']['class']) || '';
        var id = (node['$'] && node['$']['id']) || '';
        var result = '<' + type + ' class="' + classes + '" id="' + id + '">';
        result += this.decompiler.parse(node);
        result += '</' + type + '>';
        return result;
    }
};

var primitive = new FormattedText();
primitive.register();
