function RenderingRegistry()
{
    this.entities = {};
    this.defaults = {};
    this.enrichers = {};
    this.transforms = [];
    this.messageRenderers = {};
}

RenderingRegistry.prototype = {
    implements: ['registerEntity', 'unregisterEntity', 'registerDefaultRenderer', 'unregisterDefaultRenderer',
        'registerEnricher', 'unregisterEnricher', 'registerTransform', 'registerMessageRenderer',
        'getEntities', 'getDefaultRenderers', 'getEnrichers', 'getTransforms', 'getMessageRenderers'
    ],

    register: function() {
        SYMPHONY.services.make('rendering-registry', this, this.implements, true);
    },

    registerEntity : function(type, serviceName) {
        this.entities[type] = SYMPHONY.services.subscribe(serviceName);
        this.fire('new-entity-renderer', type);
    },

    unregisterEntity : function(type) {
        delete this.entities[type];
        this.fire('new-entity-renderer', type);
    },

    registerDefaultRenderer : function(type, serviceName) {
        this.defaults[type] = SYMPHONY.services.subscribe(serviceName);
    },

    unregisterDefaultRenderer : function(type) {
        delete this.defaults[type];
    },

    registerEnricher : function(serviceName) {
        this.enrichers[serviceName] = SYMPHONY.services.subscribe(serviceName);
    },

    unregisterEnricher : function(serviceName) {
        delete this.enrichers[serviceName];
    },

    registerTransform : function(serviceName) {
        this.transforms.push(SYMPHONY.services.subscribe(serviceName));
    },

    registerMessageRenderer : function(type, serviceName) {
        this.messageRenderers[type] = SYMPHONY.services.subscribe(serviceName);
    },

    getEntities : function() {
        return this.entities;
    },

    getDefaultRenderers : function() {
        return this.defaults;
    },

    getEnrichers : function() {
        return this.enrichers;
    },

    getTransforms : function() {
        return this.transforms;
    },

    getMessageRenderers : function() {
        return this.messageRenderers;
    }
}

var registry = new RenderingRegistry();
registry.register();
