function Links() {
}

Links.prototype = {
    implements : ['ready', 'render', 'decompile'],

    register : function() {
        SYMPHONY.services.make('links-primitive', this, this.implements, true);
    },

    ready : function() {
        this.modules = SYMPHONY.services.subscribe('modules');
        this.registry = SYMPHONY.services.subscribe('primitive-registry');
        this.decompiler = SYMPHONY.services.subscribe('presentationml-decompiler');
        this.parser = SYMPHONY.services.subscribe('messageml-parser');

        this.registry.registerPrimitive('a', 'links-primitive');
    },

    validUrl : function(url) {
        return url && url.indexOf('javascript:') !== 0;
    },

    render : function(messageId, element, type, data, id, node) {
        var url = (node['$'] && node['$'].id) ? data[node['$'].id] : node['$'] && node['$']['href'];

        if (!this.validUrl(url)) {
            return;
        }

        var html = $('<' + type + '>');
        html.attr('href', url);

        html.click(this.onClick.bind(this, url));

        this.parser.parse(messageId, html, node, data);

        element.append(html);
    },

    decompile : function(type, node) {
        var classes = (node['$'] && node['$']['class']) || '';
        var href = node['$'] && node['$']['href'];
        var id = (node['$'] && node['$']['id']) || '';
        var result = '<' + type + ' class="' + classes + '" href="' + href + '" id="' + id + '">';
        result += this.decompiler.parse(node);
        result += '</' + type + '>';
        return result;
    },

    onClick : function(url, e) {
        this.modules.openLink(url);
        e.preventDefault();
    }
};

var primitive = new Links();
primitive.register();
