function Html() {
}

Html.prototype = {
    implements : ['ready', 'render', 'decompile'],

    register : function() {
        SYMPHONY.services.make('html-primitive', this, this.implements, true);
    },

    ready : function() {
        var tags = ['p', 'b', 'u', 'i', 'strong', 'br', 'hr', 'ul', 'ol', 'li', 'span', 'div', 'tbody', 'thead', 'tfoot', 'table', 'th', 'tr', 'td', 'h1', 'h2', 'h3', 'h4', 'h5', 'h6'];
        this.registry = SYMPHONY.services.subscribe('primitive-registry');
        this.decompiler = SYMPHONY.services.subscribe('presentationml-decompiler');
        this.parser = SYMPHONY.services.subscribe('messageml-parser');
        this.colors = SYMPHONY.services.subscribe('render-color');

        _.each(tags, function(tag) {
            this.registry.registerPrimitive(tag, 'html-primitive');
        }, this);
    },

    render : function(messageId, element, type, data, id, node) {
        var classes = (node['$'] && node['$']['class']) || '';
        var html = $('<' + type + '>');
        classes = classes.split(' ');
        classes = classes.concat(this.colors.findColorClasses(classes.join(' ')).split(' '));
        classes = classes.join(' ');
        html.addClass(classes);
        this.parser.parse(messageId, html, node, data);
        element.append(html);
    },

    decompile : function(type, node) {
        if (type === 'br') {
            return '<br/>';
        }
        var classes = (node['$'] && node['$']['class']) || '';
        var result = '<' + type + ' class="' + classes + '">';
        result += this.decompiler.parse(node);
        result += '</' + type + '>';
        return result;
    }
};

var primitive = new Html();
primitive.register();
