function Mention() {
}

Mention.prototype = {
    implements : ['ready', 'render', 'decompile'],

    register : function() {
        SYMPHONY.services.make('mention-primitive', this, this.implements, true);
    },

    ready : function() {
        this.registry = SYMPHONY.services.subscribe('primitive-registry');
        this.decompiler = SYMPHONY.services.subscribe('presentationml-decompiler');
        this.parser = SYMPHONY.services.subscribe('messageml-parser');
        this.renderer = SYMPHONY.services.subscribe('message-renderer');
        this.registry.registerPrimitive('mention', 'mention-primitive');
    },

    render : function(messageId, element, type, data, id, node) {
        data = data && data[id];
        var name;

        if (!data) {
            var html = $('<span>');
            this.parser.parse(messageId, html, node, data);
            name = html.text();
            var url = node['$'] && node['$']['href'] || '';
            var parts = url.split('/');

            if (parts.length === 0)  {
                return;
            }

            id = parts[parts.length - 1];
        } else {
            id = data.id;
            name = data.name;
        }

        var entity = {
            id: id,
            indexStart: 0,
            indexEnd: 0,
            prettyName: name,
            screenName: name,
            text: '@' + name,
            type: 'USER_FOLLOW',
            userType: 'lc',
        }

        var html = $('<span>');
        html.append(this.renderer.renderEntity('userMentions', entity));
        element.append(html);
    },

    decompile : function(type, node) {
        var classes = (node['$'] && node['$']['class']) || '';
        var href = node['$'] && node['$']['href'];
        var result = '<' + type + ' class="' + classes + '" href="' + href + '">';
        result += this.decompiler.parse(node);
        result += '</' + type + '>';
        return result;
    },


};

var primitive = new Mention();
primitive.register();
