function Image() {
}

Image.prototype = {
    implements : ['ready', 'render', 'decompile'],

    register : function() {
        SYMPHONY.services.make('image-primitive', this, this.implements, true);
    },

    ready : function() {
        this.registry = SYMPHONY.services.subscribe('primitive-registry');
        this.decompiler = SYMPHONY.services.subscribe('presentationml-decompiler');
        this.parser = SYMPHONY.services.subscribe('messageml-parser');

        this.registry.registerPrimitive('img', 'image-primitive');
        this.registry.registerPrimitive('icon', 'image-primitive');
        this.registry.registerPrimitive('small-icon', 'image-primitive');
    },

    validUrl : function(url) {
        return url && url.indexOf('javascript:') !== 0;
    },

    render : function(messageId, element, type, data, id, node) {
        var classes = (node['$'] && node['$']['class']) || '';
        var data = data && node['$'] && data[node['$'].id] && data[node['$'].id] || {};

        var url = data.src || node['$'] && node['$']['src'];

        var typeClasses = {
            'icon': 'entity-icon',
            'small-icon': 'small-entity-icon'
        };

        if (!this.validUrl(url)) {
            return;
        }

        var html = $('<img>');
        classes = classes.split(' ');
        if (typeClasses[type]) {
            classes.unshift(typeClasses[type]);
        }
        if (data.classes) {
            classes = classes.concat(data.classes.split(' '));
        }
        classes = classes.join(' ');
        html.addClass(classes);

        html.css('max-height', '256px');
        html.attr('src', url);

        element.append(html);
    },

    decompile : function(type, node) {
        var classes = (node['$'] && node['$']['class']) || '';
        var src = node['$'] && node['$']['src'];
        var result = '<' + type + ' class="' + classes + '" src="' + src + '">';
        result += '</' + type + '>';
        return result;
    }
};

var primitive = new Image();
primitive.register();
